//var  graceChecker = require("../../common/graceChecker.js");
import graceChecker from '../../../common/graceChecker.js'
import common from '../../../common/common.js'
import util from '../../../common/util.js'

export default {
	data() {
		return {
			userInfo:[],		//用户信息
			words:'获取验证码',
			mobile:'',
			ismobile:false,
			issend:false,
			wait:60,
		}
	},
	onLoad() {
		var that = this
		var userInfo = uni.getStorageSync('userInfo');
		
		
		
	},
	methods: {
		//检测手机号输入
		onkeyinput(e){
			var that = this
			var mobile = e.detail.value;
			console.log(mobile)
			if(util.isPoneAvailable(mobile)){
				that.ismobile = true;
				that.mobile = mobile;
			}else{
				that.ismobile = false;
				that.mobile = '';
			}
		},
		
		//获取验证码
		getcode(){
			var that = this
			var mobile = that.mobile
			if(!that.ismobile){
				if(that.wait == 60){
					uni.showToast({
						title: '请输入正确手机号',
						duration: 1500,
						icon :'none'
					});
				}
				return false;
			}
			that.ismobile = false;
			that.time();
			//uni.showLoading();
			uni.request({
				url: common.sjApiUrl + 'getcode',
				method: 'GET',
				data: {
					mobile:mobile
				},
				success: res => {
					//uni.hideLoading();
					if(res.data.code > 0){
						uni.showToast({
							title: res.data.msg,
							duration: 1500,
							icon :'none'
						});
						return false;
					}else{
						that.ismobile = false;
					}
				},
				fail: () => {},
				complete: () => {}
			});
			
		},
		
		time(){
			var that = this
			var wait = that.wait
			if (wait == 0) {  
				that.ismobile = true; 
				that.words = "获取验证码";  
				that.wait = 60;  
			} else {  
				that.ismobile = false; 
				that.words = "重新发送("+wait+")";			
				wait--;
				that.wait = wait
				setTimeout(function() {  
					that.time()  
				},  
				1000)  
			}  
		},
		//登录
		userreg(){
			uni.redirectTo({
				url: '/pages/shop/userlogin/userreg',
			});
		},
		formSubmit: function (e) {
			var that = this
			var data = e.detail.value;				
			var mobile = data.mobile
			var password = data.password
			
			if(mobile == ''){					
				uni.showToast({
					title: '请输入手机号码',
					duration: 1500,
					icon :'none'
				});
				return false
			}
			
			if(!util.isPoneAvailable(mobile)){					
				uni.showToast({
					title: '手机号格式不正确',
					duration: 1500,
					icon :'none'
				});
				return false
			}
			
			if(password.length < 6){					
				uni.showToast({
					title: '密码至少6位',
					duration: 1500,
					icon :'none'
				});
				return false
			}
			console.log('开始登录');
			
			uni.showLoading({
				title: '登录中'
			});
			
			console.log(common.sjApiUrl)
			
			uni.request({
				url: common.sjApiUrl + 'merchant/userLogin',
				method: 'GET',
				data: {
					mobile:mobile,
					password:password
				},
				success: res => {
					uni.hideLoading();
					console.log(res);
					var data = res.data
					if(data.code == 0){
						uni.setStorageSync('shopInfo',data.shop);
						uni.showToast({
							title: '登录成功',
							duration: 1500,
							icon:'success',								
						});
						setTimeout(function () {
						  var jumpUrl = '/pages/shop/index/index';
						  uni.navigateTo({
						  	url: jumpUrl
						  });	
						
						}, 1000);
					}else{
						uni.showToast({
							title: data.msg,
							duration: 1500,
							icon:'none'
						});
						return false
					}
				},
				fail: () => {
					uni.hideLoading();
				},
				complete: () => {
					uni.hideLoading();
				}
			});
			
		},
		formReset: function (e) {
			console.log("清空数据")
			this.chosen = ''
		}
	}
}
